  background: rgba(0, 0, 0, 0.6);
}
/* Arrow */
.select::after {
  content: "\25BE";
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  cursor: pointer;
  pointer-events: none;
  -webkit-transition: 0.25s all ease;
  transition: 0.25s all ease;
}
/* Transition */
.select:hover::after {
  color: #da6f5b;
}
